// Create Dino Constructor
function Dinosaur(species) {
    this.species = species;
    this.image = `${this.species}.png`
    this.facts = [];
}

Dinosaur.prototype.addFact = fact => {
    this.facts.push(fact);
}

// fetch dinos
async function fetchDinos() {
    let response = await fetch('/dino.json');
    let json = await response.json();

    if (response.ok) {
        return json;
    } 

    return Promise.reject(json);
}

// Create Dino Objects
const dinos = (async () => {
    const test = await fetchDinos();
    return test;
})();

console.log('uniconr', dinos);

